.about-section {
    height: fit-content;
    padding: 20px 0px 60px 0px;
    min-height: 100vh;
    background-color: white !important;
    @media screen and (max-width: 992px) {
        border-top: 30px solid gray;
    }

    .about-top-container {
        .about-top-row {
            height: 40px;
            justify-content: center;
            align-items: center;
            display: flex;
            margin-top: 60px;
            margin-bottom: 60px;

            @media screen and (max-width: 992px) {
                margin-top: 60px !important;
                margin-bottom: 30px;
            }

            .section-header {
                margin: auto;
                h2 {
                    font-weight: bolder;
                }
            }
        }

        .about-bottom-row {
            .image-container {
                height: 100%;
                display: flex;

                img {
                    width: auto;
                    object-fit: contain;
                    padding: 0px 0px 0px 0px;

                    @media screen and (max-width: 992px) {
                        padding: 0px 0px 0px 0px;
                    }
                }
            }

            .about-text-container {
                align-items: center;
                justify-content: center;
                display: flex;
                height: fit-content;
                min-height: 100vh;

                .about-text {
                    color: gray;
                    font-weight: 400;
                    padding: 0px 30px 0px 30px !important;
                    letter-spacing: 0px;
                    text-align: left;
                    font-size: 24px;

                    a {
                        font-weight: bolder;
                        letter-spacing: 1px;
                        font-style: italic;
                        text-decoration: none;
                        color: black;
                    }

                    p {
                        margin: 0px !important;
                    }

                    @media screen and (max-width: 992px) {
                        text-align: center !important;
                        padding: 40px 0px 0px 0px !important;
                    }
                }
            }
        }
    }
}
